.section__header {
  display: flex;
  align-items: center;
  color: var(--text-color);
  margin: 2rem 0;
  gap: 0.5rem;
}
.section__header__icon {
  color: var(--main-color);
}
.category__container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.3rem;
  flex-wrap: wrap;
}
.movie {
  background-color: var(--second-bg);
  border-radius: 0.5rem;
  box-shadow: 0 0 1rem rgba(255, 255, 255, 0.1);
  overflow: hidden;
  position: relative;
  margin: 0.5rem;
  width: 180px;
  height: 260px;
}
.movie__img img {
  width: 100%;
  height: 100%;
  transform: scale(1);
  transition: all 0.3s linear;
}
.movie:hover .movie__img img {
  transform: scale(1.1);
}
.movie-info {
  position: absolute;
  background-color: var(--second-bg);
  width: calc(100% - 1rem);
  left: 0;
  right: 0;
  bottom: 0;
  height: 35px;
  color: #eee;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.5rem;
  letter-spacing: 0.5px;
}
.movie-info h5 {
  margin: 0;
  font-size: 13px;
}
.movie-info span {
  background-color: var(--main-bg);
  border-radius: 3px;
  font-weight: bold;
  padding: 0.25rem 0.5rem;
  font-size: 10px;
}
.movie-info span.green {
  color: rgb(39, 189, 39);
}
.movie-info span.orange {
  color: orange;
}
.movie-info span.red {
  color: red;
}
