.slider__container {
  margin: 6rem 0;
  height: 300px;
}
.slider__container:nth-child(1) {
  margin-top: 0;
}
.slider__header {
  height: 30px;
  padding: 1rem 0;
  color: var(--text-color);
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.slider__header h2 {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1.4rem;
}
.slider__header a {
  color: var(--stext-color);
  font-size: 1rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: all 0.3s linear;
}
.slider__header a:hover {
  color: var(--main-color);
}
.slider__header__icon {
  color: var(--main-color);
  font-size: 2rem;
}
.slider__container .swiper {
  height: 250px;
}
.swiper__slide {
  height: 250px;
  background-color: var(--second-bg);
  position: relative;
}
.swiper__slide__img {
  height: 80%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: cover;
  background-position: center;
}
.swiper__slide__vote {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.swiper__slide__vote__number {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 15px;
  font-weight: bold;
  color: #fff;
}
.swiper__slide__info {
  padding: 3px;
  height: 20%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.swiper__slide__info h3 {
  color: var(--text-color);
  margin-bottom: 3px;
  font-size: 13px;
}
.swiper__slide__info p {
  color: var(--stext-color);
  opacity: 0.7;
  font-size: 10px;
}
.slider__container .swiper-button-next,
.slider__container .swiper-button-prev {
  height: 20px;
  width: 15px;
  color: var(--main-color);
  background-color: var(--stext-color);
  padding: 0.5rem;
  border-radius: 0.5rem;
  transition: all 0.3s linear;
  border: 0.5px solid transparent;
  left: 2px;
}
.slider__container .swiper-button-next {
  left: auto;
  right: 2px;
}
.slider__container .swiper-button-next:hover,
.slider__container .swiper-button-prev:hover {
  background-color: var(--main-color);
  color: var(--text-color);
  border-color: var(--text-color);
}
.slider__container .swiper-button-next::after,
.slider__container .swiper-button-prev::after {
  font-size: 1.2rem;
  font-weight: bold;
}
