.movie__section .container {
  position: relative;
  display: flex;
  gap: 2rem;
}
.movie__section img {
  width: 30%;
  max-height: 450px !important;
}
.movie__info {
  width: calc(100% - 30%);
  background-color: var(--second-bg);
  padding: 1.2rem;
  color: var(--text-color);
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.movie__info span {
  color: var(--main-color);
  margin-right: 10px;
}
.movie__info p {
  color: var(--stext-color);
  line-height: 1.6;
  letter-spacing: 0.5px;
}
.movie__rate svg {
  color: gold;
}
.d__flex {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.movie__details {
  display: flex;
  justify-content: space-between;
  gap: 2rem;
  padding: 2rem 0;
}
.movie__season {
  background-color: var(--second-bg);
  padding: 1rem;
  width: 50%;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 1rem;
}
.season__box {
  position: relative;
  width: 150px;
  height: 200px;
  background-size: cover;
}
.season__details {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  padding: 0.5rem 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--main-color);
  color: var(--stext-color);
}
.movie__video {
  display: flex;
  align-items: center;
  justify-content: center;
}
iframe {
  padding-top: 1rem;
  max-width: 100% !important;
  max-height: 100% !important;
}
@media (max-width: 772px) {
  .movie__section .container {
    flex-wrap: wrap;
  }
  .movie__section img {
    width: 100%;
  }
  .movie__info {
    width: 100%;
  }
  .movie__season {
    width: 100%;
    display: flex;
    justify-content: center;
  }
}
