header.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 4rem;
  display: flex;
  align-items: center;
  z-index: 100;
  background-color: var(--second-bg);
  box-shadow: 0.5rem 0 0.2rem rgba(255, 255, 255, 0.2);
}
header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1.5rem;
  color: var(--text-color);
  transition: all 0.3s linear;
}
.header__logo:hover {
  color: var(--main-color);
}
.header__logo__icon {
  color: var(--main-color);
}
.header__links {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}
.header__links a {
  font-size: 1.1rem;
  color: var(--text-color);
  transition: all 0.3s linear;
}
.header__links a:hover,
.header__links a.active {
  color: var(--main-color);
}
.header__right {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 2rem;
}
.header__search {
  font-size: 2rem;
  color: var(--text-color);
  transition: all 0.3s linear;
}
.header__search:hover {
  color: var(--main-color);
}
header button.navigator {
  position: relative;
  width: 2.3rem;
  height: 2.3rem;
  background-color: transparent;
  border: none;
  outline: none;
  padding: 0;
  margin: 0;
  transition: 0.3s;
  cursor: pointer;
  display: none;
}
header button.navigator span {
  position: absolute;
  width: 100%;
  height: 0.3rem;
  background-color: var(--stext-color);
  left: 0;
}
header button.navigator span:first-child {
  top: 0;
  transition: 0.3s;
}
header button.navigator span:nth-child(2) {
  top: 50%;
  transform: translateY(-50%);
  transition: 0s;
}
header button.navigator span:last-child {
  bottom: 0;
  transition: 0.3s;
}
header button.navigator.active {
  transform: translateX(-1.15rem);
}
header button.navigator.active span:nth-child(2) {
  visibility: hidden;
}
header button.navigator.active span:first-child {
  transform: rotate(45deg) translateX(50%);
  background-color: var(--main-color);
}
header button.navigator.active span:last-child {
  transform: rotate(-45deg) translateX(50%);
  background-color: var(--main-color);
}
@media (max-width: 600px) {
  .header__links {
    position: absolute;
    top: 5rem;
    right: -100%;
    opacity: 0;
    width: 95%;
    margin: auto;
    transition: all 0.3s linear;
    display: flex;
    flex-direction: column;
    gap: 0;
    z-index: 99;
    padding: 0;
  }
  .header__links.active {
    right: 2.5%;
    opacity: 1;
  }
  .header__links a {
    background-color: var(--main-color);
    width: 90%;
    margin: 0 auto;
    padding: 1rem 0.5rem;
    box-shadow: 0 0 0.5rem var(--second-bg);
    transition: 0.7s;
  }
  .header__links a:hover,
  .header__links a.active {
    transform: translateX(0.5rem);
    background-color: var(--stext-color) !important;
    color: var(--main-color);
  }
  header button.navigator {
    display: block;
  }
}
@media (max-width: 300px) {
  .header__searchbar {
    width: 12rem;
    height: 2rem;
  }
  .header__searchbar input {
    width: 8rem;
    padding: 0 0.3rem;
    font-size: 0.8rem;
  }
  .header__searchbar button {
    width: 4rem;
    font-size: 0.8rem;
    padding: 0 0.3rem;
  }
}
