@import "./header.css";
@import "./home.css";
@import "./categories.css";
@import "./movie.css";
@import "./search.css";
* {
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  list-style: none;
  text-decoration: none;
}
:root {
  --main-color: #f25;
  --second-color: #eed202;
  --text-color: #fff;
  --stext-color: #ddd;
  --main-bg: #1c1c1c;
  --second-bg: #181818;
  --red: red;
  --green: green;
  --orangered: orangered;
}
body {
  background-color: var(--main-bg);
}
::-webkit-scrollbar {
  width: 0;
}
img {
  max-width: 100%;
  object-fit: cover;
}
section {
  padding-top: 6rem;
}
.container {
  width: 85%;
  margin: auto;
}
@media (max-width: 1124px) {
  html {
    font-size: 80%;
  }
}
@media (max-width: 992px) {
  html {
    font-size: 70%;
  }
  .container {
    width: 90%;
  }
}
@media (max-width: 772px) {
  html {
    font-size: 60%;
  }
  .container {
    width: 95%;
  }
}
@media (max-width: 400px) {
  html {
    font-size: 50%;
  }
  .container {
    width: 95%;
  }
}
