.search__section form {
  width: 100%;
  height: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.search__section form input:nth-child(1) {
  width: 80%;
  height: 100%;
  background-color: transparent;
  border-radius: 0.3rem 0 0 0.3rem;
  padding: 0 1rem;
  color: var(--text-color);
  border: 1px solid var(--stext-color);
  border-right: 0;
  transition: all 0.3s linear;
}
.search__section form input:nth-child(1):focus {
  border-color: var(--main-color);
  color: var(--main-color);
}
.search__section form input:nth-child(2) {
  height: calc(100% + 2px);
  width: 20%;
  color: var(--text-color);
  border-radius: 0 0.3rem 0.3rem 0;
  border: 1px solid var(--main-color);
  background-color: var(--main-color);
  transition: all 0.3s linear;
}
.search__section form input:nth-child(2):hover {
  color: var(--main-color);
  background-color: transparent;
}
.search__section .category__container {
  margin: 2rem 0;
}
